import util from '@/libs/util'
import common from '@/libs/common.mixin'

export default{
	mixins: [ common ],
	data () {
		return {
			data: [],
			map_scope: {'class':'班级活动','branch':'校区活动','global':'全局活动'},
			map_status: {0:'活动已禁用',1:'火热报名中',2:'活动已结束',3:'活动已取消'}
		}
	},
	methods: {
		routerLink (item) {
			this.$router.push({path: './list/'+item.event_id})
		}
	},
	computed: {
		data_uri () {
			let id = this.$route.query.id,
				uri = ''
			
			if(id==181){
				uri = util.sprintf('events?with=one_class&event_type_did=%s',id)
			}else{
				uri = util.sprintf('events?with=one_class&event_type_did=%s&bid=%s',id,this.student$.bid)
			}
			
			return uri
		}
	}
}



